'use client';

import { useEffect, useState } from 'react';
import { MapContainer, TileLayer, CircleMarker, Popup } from 'react-leaflet';
import { UMKMData, getMarkerColor } from '@/lib/data';
import 'leaflet/dist/leaflet.css';

interface MapComponentProps {
  data: UMKMData[];
}

export default function MapComponent({ data }: MapComponentProps) {
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  if (!isClient) {
    return (
      <div className="w-full h-full bg-gray-100 rounded-lg flex items-center justify-center">
        <div className="text-gray-500">Loading map...</div>
      </div>
    );
  }

  return (
    <div className="w-full h-full rounded-lg overflow-hidden shadow-lg">
      <MapContainer
        center={[-6.2, 106.8]}
        zoom={11}
        style={{ height: '100%', width: '100%' }}
        className="z-0"
      >
        <TileLayer
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        
        {data.map((umkm) => (
          <CircleMarker
            key={umkm.id}
            center={[umkm.latitude, umkm.longitude]}
            radius={8}
            pathOptions={{
              color: getMarkerColor(umkm.nilai_agunan),
              fillColor: getMarkerColor(umkm.nilai_agunan),
              fillOpacity: 0.7,
              weight: 2
            }}
          >
            <Popup className="custom-popup">
              <div className="p-2 min-w-[200px]">
                <h3 className="font-bold text-lg mb-2 text-gray-800">
                  {umkm.nama_umkm}
                </h3>
                <div className="space-y-1 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-600">Sektor:</span>
                    <span className="font-medium">{umkm.sektor}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Nilai Agunan:</span>
                    <span className="font-medium text-green-600">
                      Rp {umkm.nilai_agunan.toLocaleString('id-ID')} juta
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600">Tahun Berdiri:</span>
                    <span className="font-medium">{umkm.tahun_berdiri}</span>
                  </div>
                  <div className="mt-2 pt-2 border-t border-gray-200">
                    <p className="text-xs text-gray-500">{umkm.alamat}</p>
                  </div>
                </div>
              </div>
            </Popup>
          </CircleMarker>
        ))}
      </MapContainer>
    </div>
  );
}
